@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.pagination-wrapper {
  display: flex;
  align-items: center;

  div {
    display: flex;
    font-size: 14px;
    color: pick-color('primary', 'charcoal');
    font-weight: 500;
  }

  mat-icon {
    width: 28px;
    height: 28px;
    font-size: 28px;
  }

  .icon-wrapper {
    transition: background-color 0.2s;
    border-radius: 50px;
    padding: 3px;

    &:not(.btn-disabled):hover {
      background-color: pick-color('primary', 'fog');
    }
  }

  .content {
    display: flex;
    align-items: center;
    margin-left: 16px;
    padding: 0;

    div {
      color: pick-color('primary', 'jet');
    }

    .separator-text {
      font-weight: 400;
      color: pick-color('primary', 'charcoal');
    }
  }

  .navigation {
    display: flex;
    align-items: center;
    margin-right: 10px;
  }
}
